@import (less) "color.less";
@button-text-color: @green;
@background-color: @lightest_gray;

.dividing-line-container {
  margin: 10px 0;
  height: 44px;
  display: flex;
  align-items: center;
  position: relative;
  background-color: @background-color;

  .dividing-line {
    height: 1px;
    margin: 15px 0;
    background-color: #CCCCCC;
    flex: 0 1 100%;
  }

  .dividing-line-button {

    ::ng-deep button {
      color: @button-text-color;
      background-color: @background-color;
      border-color: @background-color !important;

      &:hover {
        color: @button-text-color;
        background-color: @background-color !important;
        border-color: @background-color !important;
      }
    }
  }

  .dividing-line-title {
    // position: absolute;
    // left: 0; top: 0; right: 0; bottom: 0;
    // background-color: @background-color;
    // color: #323232;
    // text-align: center;
    // width: fit-content;
    // padding: 0 20px;
    // font-size: 20px;
    // margin-left: auto;
    // margin-right: auto;
    display: inline-block;
  }

  .icon-title-separator {
    position: absolute;
    left: 0; top: 0; right: 0; bottom: 0;
    background-color: @background-color;
    color: #323232;
    text-align: center;
    width: fit-content;
    padding: 0 20px;
    font-size: 18px;
    margin-left: auto;
    margin-right: auto;
  }

  span + h4:before {
    content: "";
    padding-left: 10px;
  }

  span.dividing-line-title {
    line-height: 44px;
  }
}
